import { DEFAULT_REGION, TARGET_DOMAIN, TARGET_PROTOCOL } from '../options'
import { MODE } from '../util/constants'

export default class Account {
  #accountId
  #region = ''
  #targetDomain = TARGET_DOMAIN
  #dcSdkversion = ''
  /**
   * @type {('SHOPIFY' | 'WEB')} mode
   */
  #mode = 'WEB'

  constructor ({ id } = {}, region = '', targetDomain = TARGET_DOMAIN) {
    this.id = id
    if (region) {
      this.region = region
    }
    if (targetDomain) {
      this.targetDomain = targetDomain
    }
  }

  get id () {
    return this.#accountId
  }

  set id (accountId) {
    this.#accountId = accountId
  }

  get mode () {
    return this.#mode
  }

  set mode (mode) {
    this.#mode = mode
  }

  get region () {
    return this.#region
  }

  set region (region) {
    this.#region = region
  }

  get dcSDKVersion () {
    return this.#dcSdkversion
  }

  set dcSDKVersion (dcSDKVersion) {
    this.#dcSdkversion = dcSDKVersion
  }

  get targetDomain () {
    return this.#targetDomain
  }

  set targetDomain (targetDomain) {
    this.#targetDomain = targetDomain
  }

  get finalTargetDomain () {
    if (this.region) {
      return `${this.region}.${this.targetDomain}`
    } else {
      if (this.targetDomain === TARGET_DOMAIN) {
        return `${DEFAULT_REGION}.${this.targetDomain}`
      }
      return this.targetDomain
    }
  }

  get endpoint () {
    if (this.mode === MODE.SHOPIFY) {
      return 'shopify'
    }
    return 'a'
  }

  get dataPostURL () {
    return `${TARGET_PROTOCOL}//${this.finalTargetDomain}/${this.endpoint}?t=96`
  }

  get recorderURL () {
    return `${TARGET_PROTOCOL}//${this.finalTargetDomain}/r?r=1`
  }

  get emailURL () {
    return `${TARGET_PROTOCOL}//${this.finalTargetDomain}/e?r=1`
  }
}
